:root {
  --background: #f8f8f1;
  --font-color: black;
  --font-size-h1: 18px;
  --font-size-h2: 16px;
  --font-size: 13px;
 }

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8f8f1;
  background-color: var(--background);
  color: black;
  color: var(--font-color);
  line-height: 13px;
  line-height: var(--font-size);
  font-family: "JetBrains Mono", source-code-pro, Menlo, Monaco, Consolas,
    "Courier New", monospace;
  -webkit-user-select: none;
          user-select: none;
  overflow: hidden;
}

h1 {
  font-size: 18px;
  font-size: var(--font-size-h1);
  color: inherit;
}

h2 {
  font-size: 16px;
  font-size: var(--font-size-h2);
  color: inherit;
}

p {
  font-size: 13px;
  font-size: var(--font-size);
  color: inherit;
}

* {
  box-sizing: border-box;
}


canvas {
  display: block;
 
}

img {
  border:.15em  solid black;
  border:.15em  solid var(--font-color);
}

a {
  color: inherit;
}

.block {
  position: absolute;
}

.block-info {
  position: absolute;
  z-index: 1;
  border-left:.15em  solid black;
  border-left:.15em  solid var(--font-color);
  border-top:.15em  solid black;
  border-top:.15em  solid var(--font-color);
  border-bottom:.15em  solid black;
  border-bottom:.15em  solid var(--font-color);
}

hr {
  height: .1em;
  border: none;
  color: black;
  color: var(--font-color);
  background-color: black;
  background-color: var(--font-color);
  margin:0;
}

.text{
  line-height: 1.7;
  padding-left: 1em;
  padding-right: 1em;
  text-align: left;
  text-justify: distribute;
  text-align-last: left;
  text-indent: 5%;
  border-bottom: .1em solid black;
  border-bottom: .1em solid var(--font-color);

}

.text a{
  text-transform: uppercase;

}

.text img{
  align-self: center;
  margin: .5em;
  border: .1em solid black;
  border: .1em solid var(--font-color);
  margin-left: auto;
  margin-right: auto;
  width: 70%;
  display: block;
  margin-top: 1em

}

.icon {
  max-width: 20px;
  height: auto;
  border: none;

}

/* Track */
::-webkit-scrollbar-track {
  background:#f8f8f1;
  background:var(--background); 
 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background:black;
  background:var(--font-color); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
 opacity: 10%;
}
::-webkit-scrollbar {
  width: .56em;
}

.rgb-layout .react-colorful {
    width: 100%;
    max-height: 100%;
    align-items: center;
}

.rgb-layout .react-colorful__saturation {
    border-radius: 0px;
    border-bottom: none;
    max-height: 50%;
    width: 100%;
}

.rgb-layout .react-colorful__hue {
    height: 16px;
    max-width: 90%;
    width: 100%;
}

.rgb-layout .react-colorful__alpha {
    display: none;
}

.slider {
    max-width: 90%;
    margin: 0 0 15% 0;
}

.padding-left, #controls .header,.current, .rgb-layout, .slider, .generator, .world-view, .text{
    padding-left: .5em;
}

.selector {
  display: flex;
  flex-direction: row;
  align-content: flex-start;
  width:100%;
  flex-wrap: wrap;
  border-bottom: .1em solid var(--font-color);
  min-height: content;
}

.selector hr{
    width: .1em;
    height: inherit;

}

.selector button{
    background-color: var(--background);
    font-family: inherit;
    border: 0px solid ;
    align-self: baseline;
    height: 2em;
    color: inherit;
  
}
.selector .selected {
    background-color: rgb(211, 203, 203);
}

.selector button:hover{
    background-color: var(--font-color);
    color: var(--background);
}


.inverted{
    width: 20px;
    height: auto;
    -webkit-filter: invert(100%);
            filter: invert(100%);
    border: none;
}

  .selected {
    background-color: rgb(211, 203, 203) !important;
  }

 

  .wip{
    color: gray;
  }

  .body{
    display: flex;
    flex-direction: column;
  }
  .mask{
    align-content: center;
  }

  .header{
    display: flex;
    flex-direction: row;
    align-items: baseline;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0;
  }

  .emoji {
    border: none;
    background-color: var(--background);
    color: transparent;
    text-shadow: var(--font-color) 0px 0px 0px;
    opacity:.5;

  }



  .emoji:hover{
    opacity: 1;
  }

  .scroll {
    overflow-y: scroll; 

  }

  .ascii{
    max-height: 32px;
    margin-bottom: 1em;
  }

  .current {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }



  .current select{
    width: 25%;
    border: .1em solid black;
    color: var(black);
    align-self: center;
  }
#container {
  width: 100%;
  height: 85%;
  max-height: 85%;
  display: flex;
  flex-direction: row;
  justify-content:center;
  align-items: center;
  margin: 1em 0 ;
  
}

#container img {
  object-fit: cover; 
  object-position: 50% 100%;
  margin: 0em .5em ;
}


#container div{ 
 height: inherit;
 width: inherit;
 margin: 0 1em;
 display: block;
}


.button {
  font-family: inherit;
  font-size: 18px;
  line-height: inherit;
  cursor: pointer;
  color:  var(--font-color);
  background: var(--background);
  height: 64px;
  line-height: 32px;
  min-width: 32px;
  padding: "0px 0px";
